.loadMoreBtn {
    background: #00d42c;
    margin: 0 auto;
    width: 30%;
    border-radius: 15px;
    min-height: 50px;
    text-align: center;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s;
}

.loadMoreBtn p {
    font-family: 'Abel', sans-serif;
    font-size:26px;
    padding: 10px;
}

.loadMoreBtn:hover {
    opacity: 0.7;
}