.movieInfo {
    background-size: cover !important;
    background-position: center !important;
    width: 100%;
    height: 600px;
    padding: 40px 20px;
    box-sizing: border-box;
    animation: animateMovieInfo 1s;
}

@keyframes animateMovieInfo {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}

.movieInfo-content {
    max-width: 1280px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    background: rgba(0, 0, 0, 0.7);
    position: relative;
}

.movieInfo-thumb {
    width: 350px;
    height: 100%;
    overflow: hidden;
    position: absolute;
    left: 0;
}

.movieInfo-text {
    font-family: Arial, Helvetica, sans-serif;
    height: 100%;
    width: auto;
    padding: 40px;
    color: #fff;
    overflow: hidden;
    box-sizing: border-box;
    position: absolute;
    left: 360px;
}

.movieInfo-text h1 {
    font-family: 'Abel', sans-serif;
    font-size:48px;
    margin: 0;
}

.movieInfo-text h3 {
    font-size: 16px;
    line-height: 0;
    margin-top: 30px;
}

.movieInfo-text p {
    font-family: 'Abel', sans-serif;
    font-size: 18px;
    line-height: 26px;
}

.rating {
    width: 250px;
    height: 20px;
    margin-top: 20px;
    position: relative;
}

.score {
    position: absolute;
    margin: 0;
    right: 0;
    top: -3px;
}

.director {
    margin: 0;
}

.fa-film {
    position: absolute;
    bottom: 40px;
    right: 40px;
    color: #fff;
}

meter {
    width: 200px;
}