.actor {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.actor img {
    width: 80%;
    height: auto;
    box-sizing: border-box;
    float: left;
}

.actor-name {
    font-family: 'Source Sans Pro', Arial, sans-serif;
    font-size: 16px;
    font-weight: bolder;
    color: #000;
    text-align: center;
    float: left;
    margin: 5px 5px;
    box-sizing: border-box;
}

.actor-character {
    font-family: 'Source Sans Pro', Arial, sans-serif;
    font-size: 14px;
    color:#000;
    text-align: center;
    float: left;
    margin: 0 20px 10px 20px;
    box-sizing: border-box;
}

@media screen and (max-width: 480px) {
    .actor {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .actor img {
        width: 80%;
        height: auto;
        box-sizing: border-box;
        float: left;
    }

    .actor-name {
        font-family: 'Source Sans Pro', Arial, sans-serif;
        font-size: 14px;
        color: #000;
        text-align: center;
        float: left;
        margin: 10px 5px;
        box-sizing: border-box;
    }

    .actor-character {
        font-family: 'Source Sans Pro', Arial, sans-serif;
        font-size: 10px;
        color:#000;
        text-align: center;
        float: left;
        box-sizing: border-box;
    }
}